<template>
    <footer id="footer">
        <!--留言板-->
        <div class="massage">
            <div>
                <h1>留言板</h1>
            </div>
            <ul class="massageBox">
                <li v-for="(comment,index) in comments">
                    <div class="mag">
                        <img class="mag-img" :src="comment.headerimageurl" alt="">
                        <div class="mag-pepol">
                            <p>{{comment.name | nameIsNull}}</p>
                            <p>{{parseInt(comment.date) | timeIs}}</p>
                        </div>
                    </div>
                    <div class="mag-content">
                        <p>{{comment.comment}}</p>
                    </div>
                </li>
            </ul>
        </div>
        <MessagePlan id="messagePlan"/>
        <Footermassage id="footerMassage"/>
        <!--公众号-->
        <div class="wx">
            <p>微信公众号</p>
            <img src="../../../../images/Hatsune/wx/qrcode_for_gh_75b0ead78268_258.jpg" alt="">
        </div>
    </footer>
</template>

<script>
    import MessagePlan from "../../../../components/message/MessagePlan";
    import Footermassage from "../../../home/components/Footermassage";
    import {getMassageDataTop6} from "../../../../service/api";
    import timeNow from "../../../../config/timeNow";

    export default {
        name: "Footer",
        components: {
            MessagePlan,
            Footermassage
        },
        data() {
            return {
                comments: []
            };
        },filters: {
            timeIs(value) {
                return timeNow(value, false);
            },
            nameIsNull(value) {
                if (value) return value;
                else return "匿名";
            }
        },
        created() {
            //请求网络数据
            getMassageDataTop6()
                .then(response => {
                    this.comments = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../../style/statement";

    #footer {
        position: relative;
        width: 100vw;
        height: 1000px;
    }

    #messagePlan {
        width: conversionPCPX(960);
    }

    .massage {
        width: conversionPCPX(960);
        margin: 50px auto;

        .massageBox {
            display: flex;
            flex-wrap: wrap;
        }

        h1 {
            display: inline-block;
            margin: 10px;
            border-bottom: 5px solid blue;
        }

        li {
            margin: conversionPCPX(10);
            width: conversionPCPX(300);
            height: 100px;
        }
    }

    .mag {
        display: flex;
        align-items: center;

        .mag-img {
            width: conversionPCPX(50);
            height: conversionPCPX(50);
            max-height: 60px;
            max-width: 60px;
            margin: 10px;
            border-radius: 50%;
            background-color: rebeccapurple;
        }

    }

    .mag-content {
        width: 100%;
        box-sizing: border-box;
        padding: conversionPCPX(10);
    }

    #footerMassage {
        position: absolute;
        background-color: transparent;
        color: black;
        bottom: 0;
    }

    .wx {
        position: absolute;
        left: conversionPCPX(1500);
        top: 400px;
        text-align: center;

        img {
            width: conversionPCPX(258);
            max-width: 258px;
        }

        p {
            font-size: 20px;
            margin-bottom: 10px;
        }
    }

    @include styleMobile {
        #footer {
            height: conversionPX(1500);
        }
        #messagePlan {
            width: conversionPX(330);
        }
        .massage {
            width: conversionPX(330);
            margin: conversionPX(50) auto;

            .massageBox {
                display: flex;
                flex-wrap: nowrap;
                flex-direction: column;

                .mag-content {
                    p {
                        font-size: conversionPX(16);
                    }
                }
            }

            h1 {
                display: inline-block;
                margin: 10px;
                border-bottom: 5px solid blue;
            }

            li {
                margin: 0;
                width: conversionPX(330);
                height: conversionPX(100);
            }
        }
        .mag {
            .mag-img {
                width: conversionPX(50);
                height: conversionPX(50);
                max-height: none;
                max-width: none;
                margin: conversionPX(10);
            }

        }
        .wx {
            left: 50%;
            top: conversionPX(1150);
            text-align: center;
            margin-left: -0.75rem;

            img {
                width: conversionPX(150);
            }

            p {
                font-size: conversionPX(16);
                margin-bottom: conversionPX(20);
            }
        }
    }
</style>